@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@700&display=swap");

body {
  background-color: #0f4c75;
  font-family: "Roboto", sans-serif;
}

.Collapsible {
  background-color: #1b262c;
  color: white;
  border: solid 8px #1b262c;
  border-radius: 4px;
  padding: 10px;
  margin: 40px;
  margin-left: 70px;
  margin-right: 70px;
  margin-bottom: 0;
  cursor: pointer;
  text-align: center;
}

.input-level {
  background-color: #bbe1fa;
  color: black;
  margin: 10px;
  margin-left: 20px;
  padding-bottom: 20px;
  border-radius: 7px;
}

label {
  margin: 10px;
}

input {
  margin: 5px;
  padding: 3px;
  border-radius: 7px;
  background-color: white;
  border: 2px solid black;
  text-align: center;
}

button {
  padding: 6px;
  padding-left: 15px;
  padding-right: 15px;
  border-radius: 20px;
  border: solid 3px #0f4c75;
  color: white;
  background-color: rgba(0, 0, 0, 0);
  cursor: pointer;
  outline: none;

}

button:hover {
  background-color: #00aacc;
  color: #ffffff;
}

a{
    text-decoration: none;
    color:white
}

.saves {
    text-align: center;
    width: 40%;
    margin: auto;
    
}

.save-list{
    margin: 30px;
    padding: 10px;
    text-align: center;
}

.save-list > li {
    margin-bottom: 20px;
}

.save-list > li a {
    cursor: pointer;
}

.save-list > li a:hover {
    color: #00aacc;
}

.save-list > li a:active {
    color: skyblue;
}

button:disabled,
button[disabled] {
  border: 1px solid #999999;
  background-color: #cccccc;
  color: #666666;
  cursor: default;
}

a:hover{
  color: #00aacc;
}

a:active{
  color: skyblue;
}

button:active{
    background-color: skyblue;
}

.instructions {
  text-decoration: none;
}

.levels {
  background-color: #1b262c;
  margin: 20px;
  margin-top: 0px;
  margin-left: 70px;
  margin-right: 70px;
  padding: 10px;
  border: solid 8px #1b262c;
  border-radius: 5px;
  color: white;
  text-align: center;
}

.level {
  background-color: #3282b8;

  padding: 10px;
}

.ins-list {
  width: 60%;
  margin: auto;
}
